.page-header {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}


.page-header__toggle {
  display: none;
}

.page-header__menu {
  width: 100%;
  padding-left: 0;
  order: -1;
  align-self: center;
}

.page-header__logo {
  margin-top: 255px;
}

.page-header__logo:hover,
.page-header__logo:focus,
.page-header__logo:active {
  opacity: 0.7;
}

@media (max-width: $tablet-width) {
  .page-header {
    flex-direction: row;
    justify-content: space-between;
  }

  .page-header__logo {
    margin-top: 25px;
    margin-left: 21px;
  }

  .page-header__toggle {
    display: inline-block;
    width: 50px;
    height: 50px;
    border: none;
    background-image: url("../img/icon-menu.svg");
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-color: #0ad9c6;
  }
}

@media (max-width: $tablet-width) {
  .page-header__logo {
    margin-left: 17px;
  }
}
